function getForcedFallbackMode() {
    const params = new URLSearchParams(window.location.search);
    const param = (params.get('forceFallback') || '').toLowerCase().trim();
    let stored = '';
    try {
        stored = (localStorage.getItem('forceFallback') || '').toLowerCase().trim();
    } catch (err) {
        stored = '';
    }
    const mode = param || stored;
    if (!mode) return null;
    if (mode === 'resize' || mode === 'encode') return mode;
    if (mode === 'both' || mode === 'all' || mode === 'true' || mode === '1') return 'both';
    return null;
}

function getPreviewDelayMs(forSettings = false) {
    const select = document.getElementById('previewDelay');
    if (!select) return 500;
    const value = select.value;
    if (value === 'off') {
        return forSettings ? 500 : null;
    }
    const seconds = parseFloat(value);
    if (!Number.isFinite(seconds)) {
        return forSettings ? 500 : null;
    }
    return Math.round(seconds * 1000);
}

function getPreviewDelayMsDrag() {
    return getPreviewDelayMs(false);
}

function getPreviewDelayMsMouseup() {
    const select = document.getElementById('previewDelay');
    if (!select) return 180;
    const value = select.value;
    if (value === 'off') {
        return 180;
    }
    const seconds = parseFloat(value);
    if (!Number.isFinite(seconds)) {
        return 180;
    }
    return Math.round(seconds * 1000);
}

