function getResizeEngineSettings() {
    const engine = document.getElementById('resizeEngine')?.value || 'jsquash';
    const usePhotonResize = engine === 'photon';
    const jsquashMethod = document.getElementById('jsquashResizeMethod')?.value || DEFAULT_JSQUASH_METHOD;
    const photonRaw = parseInt(document.getElementById('photonResizeFilter')?.value, 10);
    const photonSamplingFilter = Number.isFinite(photonRaw)
        ? Math.min(5, Math.max(1, photonRaw))
        : DEFAULT_PHOTON_SAMPLING_FILTER;
    return { usePhotonResize, jsquashMethod, photonSamplingFilter, engine };
}

