window.addEventListener('resize', () => {
    if (!canResizeMainPreview() || !imagesArea) return;
    const currentWidth = imagesArea.getBoundingClientRect().width;
    const clamped = clampMainSplitWidth(currentWidth);
    if (Math.abs(clamped - currentWidth) > 1) {
        imagesArea.style.flex = `0 0 ${clamped}px`;
    }
    updatePreviewZoomLabels();
    if (isSplitPreview) {
        updateSplitLayoutForContainer();
    }
});

if (previewSplitResizer) {
    previewSplitResizer.addEventListener('mousedown', (e) => {
        if (!previewSplit || !previewLeft) return;
        if (!canResizeSplitPreview()) return;
        isPreviewSplitResizing = true;
        if (splitOrientation === 'vertical') {
            previewSplitStartPos = e.clientY;
            previewSplitStartSize = previewLeft.getBoundingClientRect().height;
            document.body.classList.add('resizing-vertical');
        } else {
            previewSplitStartPos = e.clientX;
            previewSplitStartSize = previewLeft.getBoundingClientRect().width;
            document.body.classList.add('resizing');
        }
        previewSplitResizer.classList.add('resizing');
        e.preventDefault();
    });

    previewSplitResizer.addEventListener('pointerdown', (e) => {
        if (e.pointerType === 'mouse') return;
        if (!previewSplit || !previewLeft) return;
        if (!canResizeSplitPreview()) return;
        isPreviewSplitResizing = true;
        if (splitOrientation === 'vertical') {
            previewSplitStartPos = e.clientY;
            previewSplitStartSize = previewLeft.getBoundingClientRect().height;
            document.body.classList.add('resizing-vertical');
        } else {
            previewSplitStartPos = e.clientX;
            previewSplitStartSize = previewLeft.getBoundingClientRect().width;
            document.body.classList.add('resizing');
        }
        previewSplitResizer.classList.add('resizing');
        if (previewSplitResizer.setPointerCapture) {
            previewSplitResizer.setPointerCapture(e.pointerId);
        }
        e.preventDefault();
    });
}

document.addEventListener('mousemove', (e) => {
    if (!isPreviewSplitResizing || !previewSplit || !previewLeft) return;
    if (!canResizeSplitPreview()) {
        isPreviewSplitResizing = false;
        previewSplitResizer?.classList.remove('resizing');
        document.body.classList.remove('resizing');
        document.body.classList.remove('resizing-vertical');
        return;
    }
    const isFullscreen = isPreviewPaneFullscreen();
    if (splitOrientation === 'vertical') {
        const diff = e.clientY - previewSplitStartPos;
        const containerHeight = previewSplit.getBoundingClientRect().height;
        const resizerHeight = previewSplitResizer ? previewSplitResizer.getBoundingClientRect().height : 6;
        const bounds = getSplitBounds(containerHeight, resizerHeight);
        let newTop = previewSplitStartSize + diff;
        newTop = Math.max(bounds.min, Math.min(bounds.max, newTop));
        previewLeft.style.flex = `0 0 ${newTop}px`;
        if (isFullscreen) {
            lastSplitPositionVerticalFullscreen = newTop;
        } else {
            lastSplitPositionVertical = newTop;
        }
    } else {
        const diff = e.clientX - previewSplitStartPos;
        const containerWidth = previewSplit.getBoundingClientRect().width;
        const resizerWidth = previewSplitResizer ? previewSplitResizer.getBoundingClientRect().width : 6;
        const bounds = getSplitBounds(containerWidth, resizerWidth);
        let newLeft = previewSplitStartSize + diff;
        newLeft = Math.max(bounds.min, Math.min(bounds.max, newLeft));
        previewLeft.style.flex = `0 0 ${newLeft}px`;
        if (isFullscreen) {
            lastSplitPositionFullscreen = newLeft;
        } else {
            lastSplitPosition = newLeft;
        }
    }
    updatePreviewZoomLabels();
});

