document.addEventListener('fullscreenchange', () => {
    const previewPane = document.getElementById('previewPane');
    const isFullscreen = previewPane && document.fullscreenElement === previewPane;
    previewFullscreen = Boolean(isFullscreen);
    if (!isFullscreen && pendingClosePreviewAfterFullscreen) {
        const pending = pendingClosePreviewAfterFullscreen;
        pendingClosePreviewAfterFullscreen = null;
        finalizeClosePreview(Boolean(pending.skipSplitReset));
        return;
    }
    if (!isFullscreen && forcedFullscreenPreview && window.innerWidth < PREVIEW_FULLSCREEN_BREAKPOINT) {
        if (previewPane?.classList.contains('active')) {
            closePreview();
            return;
        }
    }
    if (isFullscreen) {
        document.body.classList.remove('preview-fullscreen');
        fullscreenSplitNeedsReset = true;
        lastSplitPositionFullscreen = null;
        lastSplitPositionVerticalFullscreen = null;
    } else {
        document.body.classList.remove('preview-fullscreen');
    }
    updatePreviewFullscreenButton();
    requestAnimationFrame(() => {
        updateSplitLayoutForContainer();
        if (isFullscreen) {
            storeCurrentSplitSize(true);
        }
        updatePreviewZoomLabels();
    });
});

