function updatePreviewCropIfVisible(img) {
    if (!selectedImageId || selectedImageId !== img.id) return;
    const previewWrapper = document.getElementById('previewWrapper');
    if (!previewWrapper) return;
    const cropBox = previewWrapper.querySelector('.crop-box');
    if (cropBox) {
        updateCropBoxStyle(cropBox, img, true);
    }
}

function updateGridCropIfVisible(img) {
    const grid = document.getElementById('imagesGrid');
    if (!grid) return;
    const card = grid.querySelector(`[data-image-id="${img.id}"]`);
    if (!card) return;
    const cropBox = card.querySelector('.crop-box');
    if (cropBox) {
        updateCropBoxStyle(cropBox, img, false);
    }
}

function clampCropToBounds(crop, img) {
    const clamped = { ...crop };
    if (clamped.x < 0) {
        clamped.width += clamped.x;
        clamped.x = 0;
    }
    if (clamped.y < 0) {
        clamped.height += clamped.y;
        clamped.y = 0;
    }
    if (clamped.x + clamped.width > img.width) {
        clamped.width = img.width - clamped.x;
    }
    if (clamped.y + clamped.height > img.height) {
        clamped.height = img.height - clamped.y;
    }
    return clamped;
}

function clampCropToBoundsWithAspect(crop, img, aspect) {
    const clamped = { ...crop };
    const maxWidth = img.width;
    const maxHeight = img.height;
    if (clamped.width > maxWidth) {
        clamped.width = maxWidth;
        clamped.height = clamped.width / aspect;
    }
    if (clamped.height > maxHeight) {
        clamped.height = maxHeight;
        clamped.width = clamped.height * aspect;
    }
    if (clamped.width > maxWidth) {
        clamped.width = maxWidth;
        clamped.height = clamped.width / aspect;
    }
    clamped.x = Math.max(0, Math.min(clamped.x, img.width - clamped.width));
    clamped.y = Math.max(0, Math.min(clamped.y, img.height - clamped.height));
    return clamped;
}

function applyCropUpdate(img, newCrop) {
    if (!img || !newCrop) return;
    img.crop = newCrop;
    const lastChangeId = ++cropChangeId;
    img._lastCropChangeId = lastChangeId;
    updatePreviewCropIfVisible(img);
    updateGridCropIfVisible(img);
    scheduleExportPreviewRefresh({ preserveImage: true }, getPreviewDelayMsMouseup(), lastChangeId);
}

function resizeCropFromCenter(img, direction, step, aspect) {
    const crop = img.crop || { x: 0, y: 0, width: img.width, height: img.height };
    const minSize = 20;
    const cx = crop.x + crop.width / 2;
    const cy = crop.y + crop.height / 2;
    let newWidth = crop.width + (direction * 2 * step);
    let newHeight = crop.height + (direction * 2 * step);

    if (aspect) {
        if (aspect >= 1) {
            const minWidth = Math.max(minSize, minSize * aspect);
            newWidth = Math.max(minWidth, newWidth);
            newHeight = newWidth / aspect;
        } else {
            const minHeight = Math.max(minSize, minSize / aspect);
            newHeight = Math.max(minHeight, newHeight);
            newWidth = newHeight * aspect;
        }
    } else {
        newWidth = Math.max(minSize, newWidth);
        newHeight = Math.max(minSize, newHeight);
    }

    let next = {
        x: cx - newWidth / 2,
        y: cy - newHeight / 2,
        width: newWidth,
        height: newHeight
    };

    next = aspect
        ? clampCropToBoundsWithAspect(next, img, aspect)
        : clampCropToBounds(next, img);

    return next;
}

function nudgeCrop(action, step) {
    if (!selectedImageId) return;
    if (!shouldShowCropOverlay()) return;
    const img = images.find(i => i.id === selectedImageId);
    if (!img || !img.crop) return;
    const aspect = isAspectLockedMode() ? currentAspectRatio : null;
    const crop = { ...img.crop };
    let next = { ...crop };

    switch (action) {
        case 'up':
            next.y -= step;
            next = clampCropToBounds(next, img);
            break;
        case 'down':
            next.y += step;
            next = clampCropToBounds(next, img);
            break;
        case 'left':
            next.x -= step;
            next = clampCropToBounds(next, img);
            break;
        case 'right':
            next.x += step;
            next = clampCropToBounds(next, img);
            break;
        case 'grow':
            next = resizeCropFromCenter(img, 1, step, aspect);
            break;
        case 'shrink':
            next = resizeCropFromCenter(img, -1, step, aspect);
            break;
        default:
            return;
    }

    applyCropUpdate(img, next);
}

function updatePreviewNudgeControls() {
    const footer = document.getElementById('previewFooter');
    if (!footer) return;
    const previewPane = document.getElementById('previewPane');
    const canShow = Boolean(
        previewPane?.classList.contains('active')
        && selectedImageId
        && shouldShowCropOverlay()
    );
    footer.classList.toggle('hidden', !canShow);
}

function constrainCropWithAspect(crop, startCrop, position, img, aspect) {
    const result = { ...crop };

    const anchorLeft = position.includes('w');
    const anchorTop = position.includes('n');
    const anchorRight = position.includes('e');
    const anchorBottom = position.includes('s');

    if (anchorLeft) {
        result.width = startCrop.x + startCrop.width - result.x;
    }
    if (anchorTop) {
        result.height = startCrop.y + startCrop.height - result.y;
    }

    if (anchorRight || anchorLeft) {
        result.height = result.width / aspect;
        if (anchorTop) {
            result.y = startCrop.y + startCrop.height - result.height;
        }
    } else if (anchorBottom || anchorTop) {
        result.width = result.height * aspect;
        if (anchorLeft) {
            result.x = startCrop.x + startCrop.width - result.width;
        }
    }

    // Fit within bounds while keeping aspect
    if (result.x < 0) {
        result.x = 0;
        result.width = startCrop.x + startCrop.width;
        result.height = result.width / aspect;
    }
    if (result.y < 0) {
        result.y = 0;
        result.height = startCrop.y + startCrop.height;
        result.width = result.height * aspect;
    }
    if (result.x + result.width > img.width) {
        result.width = img.width - result.x;
        result.height = result.width / aspect;
        if (anchorTop) {
            result.y = startCrop.y + startCrop.height - result.height;
        }
    }
    if (result.y + result.height > img.height) {
        result.height = img.height - result.y;
        result.width = result.height * aspect;
        if (anchorLeft) {
            result.x = startCrop.x + startCrop.width - result.width;
        }
    }

    return result;
}

function showPreview(id) {
    selectedImageId = id;
    const img = images.find(i => i.id === id);
    const previewPane = document.getElementById('previewPane');
    const previewWrapper = document.getElementById('previewWrapper');
    const previewTitle = document.getElementById('previewTitle');
    const exportOverlay = document.querySelector('.export-preview-overlay');

    if (!img) {
        forceResetPreviewState();
        return;
    }
    if (!previewPane || !previewWrapper || !previewTitle) {
        return;
    }
    if (exportPreviewDebounceTimer) {
        clearTimeout(exportPreviewDebounceTimer);
        exportPreviewDebounceTimer = null;
    }

    previewPane.classList.add('active');
    document.body.classList.add('preview-active');
    updateMainResizerState();
    updatePreviewFullscreenButton();
    previewTitle.dataset.baseTitle = img.name;
    previewTitle.textContent = img.name;

    previewWrapper.innerHTML = '';

    const imageContainer = document.createElement('div');
    imageContainer.style.position = 'relative';
    imageContainer.style.display = 'block';
    imageContainer.style.lineHeight = '0';
    imageContainer.style.width = '100%';
    imageContainer.style.height = '100%';

    const image = document.createElement('img');
    image.className = 'preview-image';
    image.style.width = '100%';
    image.style.height = '100%';
    image.style.objectFit = 'contain';
    image.style.position = 'relative';

    imageContainer.appendChild(image);
    previewWrapper.appendChild(imageContainer);

    // Wait for image to load and render
    image.onload = () => {
        attachCropBox(imageContainer, image, img, true);
        requestAnimationFrame(() => {
            const cropBox = imageContainer.querySelector('.crop-box');
            if (cropBox) {
                updateCropBoxStyle(cropBox, img, true);
            }
            updatePreviewZoomLabels();
        });
    };
    image.src = img.src;

    if (image.complete && image.naturalWidth > 0) {
        requestAnimationFrame(() => {
            attachCropBox(imageContainer, image, img, true);
        });
    }

    updateSelectedCard();
    updatePreviewNudgeControls();
    const forceFullscreen = window.innerWidth < PREVIEW_FULLSCREEN_BREAKPOINT;
    forcedFullscreenPreview = forceFullscreen;
    if (forceFullscreen) {
        if (isSplitPreview) {
            setSplitPreview(false, true);
        }
        if (!isPreviewPaneFullscreen()) {
            togglePreviewFullscreen();
        }
    }
    const wantsSplit = lastExportPreviewMode === 'split'
        || lastExportPreviewMode === 'split-horizontal'
        || lastExportPreviewMode === 'split-vertical';
    if (wantsSplit && !forceFullscreen) {
        const orientation = lastExportPreviewMode === 'split-vertical' ? 'vertical' : 'horizontal';
        if (!isSplitPreview || splitOrientation !== orientation) {
            setSplitPreview(true, true, orientation);
            return;
        }
        openExportPreview(img, { preserveImage: true });
        return;
    } else if (lastExportPreviewMode === 'full') {
        if (isSplitPreview) {
            setSplitPreview(false, true);
        }
        openExportPreview(img, { preserveImage: true });
    } else if (isSplitPreview) {
        openExportPreview(img, { preserveImage: true });
    } else if (exportOverlay) {
        openExportPreview(img, { preserveImage: true });
    }
}

function finalizeClosePreview(skipSplitReset = false) {
    document.getElementById('previewPane').classList.remove('active');
    document.body.classList.remove('preview-active');
    updateMainResizerState();
    selectedImageId = null;
    forcedFullscreenPreview = false;
    previewFullscreen = false;
    document.body.classList.remove('preview-fullscreen');
    updatePreviewFullscreenButton();
    exportPreviewRequestId += 1;
    if (exportPreviewDebounceTimer) {
        clearTimeout(exportPreviewDebounceTimer);
        exportPreviewDebounceTimer = null;
    }
    isExportPreviewVisible = false;
    updateTestExportButton();
    const wasSplit = isSplitPreview;
    if (!skipSplitReset) {
        if (wasSplit) {
            setSplitPreview(false);
            lastExportPreviewMode = splitOrientation === 'vertical' ? 'split-vertical' : 'split-horizontal';
        } else {
            isSplitPreview = false;
            const previewContent = document.getElementById('previewContent');
            if (previewContent) {
                previewContent.classList.remove('split-active');
                previewContent.classList.remove('split-vertical');
            }
            updateTestExportButton();
            updatePreviewLayoutButtons();
            lastExportPreviewMode = 'none';
        }
    }
    if (wasSplit) {
        const host = document.getElementById('exportPreviewHost');
        if (host) {
            host.innerHTML = '';
        }
    }
    // Defensive cleanup to avoid leaving split layout artifacts.
    isSplitPreview = false;
    if (previewLeft) {
        previewLeft.style.flex = '';
    }
    if (imagesArea) {
        imagesArea.style.flex = '';
    }
    const previewContent = document.getElementById('previewContent');
    if (previewContent) {
        previewContent.classList.remove('split-active');
        previewContent.classList.remove('split-vertical');
    }
    updateSelectedCard();
    updatePreviewNudgeControls();
}

function closePreview(skipSplitReset = false) {
    const previewPaneEl = document.getElementById('previewPane');
    if (document.fullscreenElement && document.fullscreenElement === previewPaneEl) {
        pendingClosePreviewAfterFullscreen = { skipSplitReset };
        document.exitFullscreen().catch(() => {
            const pending = pendingClosePreviewAfterFullscreen;
            pendingClosePreviewAfterFullscreen = null;
            finalizeClosePreview(Boolean(pending?.skipSplitReset));
        });
        return;
    }
    finalizeClosePreview(skipSplitReset);
}

function forceResetPreviewState() {
    const previewPane = document.getElementById('previewPane');
    const previewContent = document.getElementById('previewContent');
    const previewWrapper = document.getElementById('previewWrapper');
    const exportHost = document.getElementById('exportPreviewHost');
    const overlay = document.querySelector('.export-preview-overlay');
    if (overlay) overlay.remove();
    if (exportHost) exportHost.innerHTML = '';
    if (previewWrapper) previewWrapper.innerHTML = '';
    if (previewPane) previewPane.classList.remove('active');
    document.body.classList.remove('preview-active');
    if (previewContent) {
        previewContent.classList.remove('split-active');
        previewContent.classList.remove('split-vertical');
    }
    if (previewLeft) {
        previewLeft.style.flex = '';
    }
    if (imagesArea) {
        imagesArea.style.flex = '';
    }
    isSplitPreview = false;
    isExportPreviewVisible = false;
    lastExportPreviewMode = 'none';
    selectedImageId = null;
    forcedFullscreenPreview = false;
    updateMainResizerState();
    updateTestExportButton();
    updatePreviewLayoutButtons();
    updatePreviewNudgeControls();
    persistPreferences({ lastExportPreviewMode: 'none' });
}

function testExport() {
    if (!selectedImageId) return;
    if (isExportPreviewVisible && !isSplitPreview) {
        closeExportPreview();
        lastExportPreviewMode = 'none';
        updateTestExportButton();
        return;
    }

    const img = images.find(i => i.id === selectedImageId);
    openExportPreview(img);
    lastExportPreviewMode = 'full';
    updateTestExportButton();
}

function closeExportPreview(preserveMode = false) {
    const overlay = document.querySelector('.export-preview-overlay');
    if (overlay) overlay.remove();
    ensureExportPreviewPlaceholder();
    isExportPreviewVisible = false;
    updateTestExportButton();
    if (!preserveMode) {
        lastExportPreviewMode = 'none';
    }
    if (!isSplitPreview && selectedImageId) {
        const img = images.find(i => i.id === selectedImageId);
        if (img) {
            showPreview(selectedImageId);
        }
    }
}

function getExportPreviewContainer() {
    const previewContent = document.getElementById('previewContent');
    const exportHost = document.getElementById('exportPreviewHost');
    if (isSplitPreview && exportHost) {
        return exportHost;
    }
    return previewContent;
}

function ensureExportPreviewPlaceholder() {
    if (!isSplitPreview) return;
    const host = document.getElementById('exportPreviewHost');
    if (!host) return;
    if (host.querySelector('.export-preview-overlay')) return;
    host.innerHTML = '<div class="export-preview-placeholder">Test Export to compare</div>';
}

function updateExportPreviewSizeFromImage(overlay, exportImg, sizeBytes, expectedWidth, expectedHeight) {
    if (!overlay || !exportImg) return;
    const sizeEl = overlay.querySelector('.export-preview-size');
    if (!sizeEl) return;
    const actualWidth = exportImg.naturalWidth;
    const actualHeight = exportImg.naturalHeight;
    const parts = [];
    if (Number.isFinite(sizeBytes) && sizeBytes >= 0) {
        parts.push(`${(sizeBytes / 1024).toFixed(1)} KB`);
    }
    if (Number.isFinite(actualWidth) && Number.isFinite(actualHeight) && actualWidth > 0 && actualHeight > 0) {
        parts.push(`${actualWidth}x${actualHeight}`);
    }
    const hasExpected = Number.isFinite(expectedWidth)
        && Number.isFinite(expectedHeight)
        && expectedWidth > 0
        && expectedHeight > 0;
    const mismatch = hasExpected
        && Number.isFinite(actualWidth)
        && Number.isFinite(actualHeight)
        && actualWidth > 0
        && actualHeight > 0
        && (expectedWidth !== actualWidth || expectedHeight !== actualHeight);
    if (mismatch) {
        parts.push(`expected ${expectedWidth}x${expectedHeight}`);
        console.warn('Export preview size mismatch', {
            expected: { width: expectedWidth, height: expectedHeight },
            actual: { width: actualWidth, height: actualHeight }
        });
    }
    sizeEl.style.color = mismatch ? 'var(--warning)' : 'var(--text-secondary)';
    if (parts.length) {
        const label = mismatch ? `MISMATCH: ${parts.join('  ')}` : parts.join('  ');
        sizeEl.textContent = `(${label})`;
    } else {
        sizeEl.textContent = '';
    }
}

function getContentInnerSize(el) {
    if (!el) return { width: 0, height: 0 };
    const style = window.getComputedStyle(el);
    const paddingX = parseFloat(style.paddingLeft) + parseFloat(style.paddingRight);
    const paddingY = parseFloat(style.paddingTop) + parseFloat(style.paddingBottom);
    const width = el.clientWidth - (Number.isFinite(paddingX) ? paddingX : 0);
    const height = el.clientHeight - (Number.isFinite(paddingY) ? paddingY : 0);
    return {
        width: Math.max(0, width),
        height: Math.max(0, height)
    };
}

function getExportPreviewFitScale(scrollEl, exportImg) {
    if (!scrollEl || !exportImg || !exportImg.naturalWidth || !exportImg.naturalHeight) return 1;
    const inner = getContentInnerSize(scrollEl);
    if (inner.width <= 0 || inner.height <= 0) return 1;
    const scale = Math.min(inner.width / exportImg.naturalWidth, inner.height / exportImg.naturalHeight, 1);
    return Number.isFinite(scale) ? scale : 1;
}

function setExportPreviewZoomMode(overlay, mode, options = {}) {
    if (!overlay) return;
    const scrollEl = overlay.querySelector('.export-preview-scroll');
    if (!scrollEl) return;
    const normalized = ['fit', '100', '200', '400'].includes(mode) ? mode : 'fit';
    overlay.dataset.zoomMode = normalized;
    scrollEl.classList.toggle('zoom-actual', normalized !== 'fit');
    const controls = overlay.querySelector('.export-preview-zoom-controls');
    if (controls) {
        controls.querySelectorAll('button[data-zoom]').forEach(btn => {
            btn.classList.toggle('active', btn.dataset.zoom === normalized);
        });
    }
    if (normalized === 'fit' && options.resetScroll !== false) {
        scrollEl.scrollLeft = 0;
        scrollEl.scrollTop = 0;
    }
}

function applyExportPreviewZoom(overlay, exportImg) {
    if (!overlay || !exportImg || !exportImg.naturalWidth || !exportImg.naturalHeight) return;
    const mode = overlay.dataset.zoomMode || 'fit';
    if (mode === 'fit') {
        exportImg.style.width = '';
        exportImg.style.height = '';
        return;
    }
    let scale = 1;
    if (mode === '200') scale = 2;
    if (mode === '400') scale = 4;
    exportImg.style.width = `${Math.max(1, Math.round(exportImg.naturalWidth * scale))}px`;
    exportImg.style.height = `${Math.max(1, Math.round(exportImg.naturalHeight * scale))}px`;
}

function getExportPreviewScale(overlay, exportImg) {
    if (!overlay || !exportImg) return 1;
    const mode = overlay.dataset.zoomMode || 'fit';
    if (mode === 'fit') {
        return getExportPreviewFitScale(overlay.querySelector('.export-preview-scroll'), exportImg);
    }
    if (mode === '200') return 2;
    if (mode === '400') return 4;
    return 1;
}

function updateExportPreviewZoomUI(overlay, exportImg) {
    if (!overlay) return;
    const scrollEl = overlay.querySelector('.export-preview-scroll');
    const controls = overlay.querySelector('.export-preview-zoom-controls');
    if (!scrollEl || !controls) return;
    controls.style.display = 'inline-flex';
    setExportPreviewZoomMode(overlay, overlay.dataset.zoomMode || 'fit', { resetScroll: false });
    applyExportPreviewZoom(overlay, exportImg);
}

function bindExportPreviewPanHandlers(content) {
    if (!content || content.dataset.panBound) return;
    content.dataset.panBound = 'true';
    let isPanning = false;
    let startX = 0;
    let startY = 0;
    let startLeft = 0;
    let startTop = 0;

    content.addEventListener('pointerdown', (e) => {
        if (e.button !== 0) return;
        isPanning = true;
        startX = e.clientX;
        startY = e.clientY;
        startLeft = content.scrollLeft;
        startTop = content.scrollTop;
        content.classList.add('panning');
        content.setPointerCapture(e.pointerId);
        e.preventDefault();
    });

    content.addEventListener('pointermove', (e) => {
        if (!isPanning) return;
        const dx = e.clientX - startX;
        const dy = e.clientY - startY;
        content.scrollLeft = startLeft - dx;
        content.scrollTop = startTop - dy;
    });

    const endPan = (e) => {
        if (!isPanning) return;
        isPanning = false;
        content.classList.remove('panning');
        if (e && content.hasPointerCapture?.(e.pointerId)) {
            content.releasePointerCapture(e.pointerId);
        }
    };

    content.addEventListener('pointerup', endPan);
    content.addEventListener('pointercancel', endPan);
}

function bindExportPreviewZoomControls(overlay) {
    if (!overlay || overlay.dataset.zoomBound) return;
    overlay.dataset.zoomBound = 'true';
    const controls = overlay.querySelector('.export-preview-zoom-controls');
    if (!controls) return;
    controls.addEventListener('click', (e) => {
        const btn = e.target.closest('button[data-zoom]');
        if (!btn) return;
        const scrollEl = overlay.querySelector('.export-preview-scroll');
        const exportImg = overlay.querySelector('.export-preview-image');
        let centerX = null;
        let centerY = null;
        if (scrollEl && exportImg) {
            const prevScale = getExportPreviewScale(overlay, exportImg) || 1;
            if (prevScale > 0) {
                centerX = (scrollEl.scrollLeft + scrollEl.clientWidth / 2) / prevScale;
                centerY = (scrollEl.scrollTop + scrollEl.clientHeight / 2) / prevScale;
            }
        }
        setExportPreviewZoomMode(overlay, btn.dataset.zoom, { resetScroll: false });
        updateExportPreviewZoomUI(overlay, exportImg);
        if (scrollEl && exportImg && centerX !== null && centerY !== null) {
            requestAnimationFrame(() => {
                const nextScale = getExportPreviewScale(overlay, exportImg) || 1;
                const targetLeft = centerX * nextScale - scrollEl.clientWidth / 2;
                const targetTop = centerY * nextScale - scrollEl.clientHeight / 2;
                const maxLeft = Math.max(0, scrollEl.scrollWidth - scrollEl.clientWidth);
                const maxTop = Math.max(0, scrollEl.scrollHeight - scrollEl.clientHeight);
                scrollEl.scrollLeft = Math.min(maxLeft, Math.max(0, targetLeft));
                scrollEl.scrollTop = Math.min(maxTop, Math.max(0, targetTop));
            });
        }
        updatePreviewZoomLabels();
    });
}

function openExportPreview(img, options = {}) {
    const { preserveImage = false } = options;
    const previewContent = document.getElementById('previewContent');
    const exportHost = document.getElementById('exportPreviewHost');
    const container = getExportPreviewContainer();
    let overlay = container ? container.querySelector('.export-preview-overlay') : null;
    let content;
    const isSingleView = !isSplitPreview && container === previewContent;
    const requestId = ++exportPreviewRequestId;
    if (!overlay && exportHost && previewContent) {
        const other = container === previewContent ? exportHost : previewContent;
        const existingOther = other.querySelector('.export-preview-overlay');
        if (existingOther) {
            existingOther.remove();
        }
    }
    if (!overlay) {
        overlay = document.createElement('div');
        overlay.className = 'export-preview-overlay';

        const header = document.createElement('div');
        header.className = 'export-preview-header';
        const titleRow = document.createElement('div');
        titleRow.className = 'export-preview-title-row';
        const titleSpan = document.createElement('span');
        titleSpan.className = 'export-preview-title';
        titleSpan.dataset.baseTitle = 'Export Preview';
        titleSpan.textContent = 'Export Preview';
        const sizeSpan = document.createElement('span');
        sizeSpan.className = 'export-preview-size';
        titleRow.appendChild(titleSpan);
        titleRow.appendChild(sizeSpan);

        const headerActions = document.createElement('div');
        headerActions.className = 'export-preview-header-actions';

        const zoomControls = document.createElement('div');
        zoomControls.className = 'export-preview-zoom-controls';
        zoomControls.style.display = 'none';
        const fitBtn = document.createElement('button');
        fitBtn.className = 'btn export-zoom-btn';
        fitBtn.dataset.zoom = 'fit';
        fitBtn.textContent = 'Fit';
        const fullBtn = document.createElement('button');
        fullBtn.className = 'btn export-zoom-btn';
        fullBtn.dataset.zoom = '100';
        fullBtn.textContent = '100%';
        const doubleBtn = document.createElement('button');
        doubleBtn.className = 'btn export-zoom-btn';
        doubleBtn.dataset.zoom = '200';
        doubleBtn.textContent = '200%';
        const quadBtn = document.createElement('button');
        quadBtn.className = 'btn export-zoom-btn';
        quadBtn.dataset.zoom = '400';
        quadBtn.textContent = '400%';
        zoomControls.appendChild(fitBtn);
        zoomControls.appendChild(fullBtn);
        zoomControls.appendChild(doubleBtn);
        zoomControls.appendChild(quadBtn);
        headerActions.appendChild(zoomControls);

        if (!isSplitPreview) {
            const closeBtn = document.createElement('button');
            closeBtn.className = 'icon-btn';
            closeBtn.textContent = '';
            closeBtn.type = 'button';
            closeBtn.addEventListener('pointerdown', (e) => {
                e.stopPropagation();
            });
            closeBtn.addEventListener('click', (e) => {
                e.preventDefault();
                e.stopPropagation();
                closeExportPreview();
                updateTestExportButton();
            });
            headerActions.appendChild(closeBtn);
        }

        header.appendChild(titleRow);
        header.appendChild(headerActions);

        content = document.createElement('div');
        content.className = 'export-preview-content';
        const scroll = document.createElement('div');
        scroll.className = 'export-preview-scroll';
        content.appendChild(scroll);

        overlay.appendChild(header);
        overlay.appendChild(content);
        if (container) {
            if (isSingleView) {
                container.appendChild(overlay);
            } else {
                container.innerHTML = '';
                container.appendChild(overlay);
            }
        }
        isExportPreviewVisible = true;
        updateTestExportButton();
        bindExportPreviewPanHandlers(scroll);
        bindExportPreviewZoomControls(overlay);
        setExportPreviewZoomMode(overlay, 'fit');
    } else {
        content = overlay.querySelector('.export-preview-content');
        isExportPreviewVisible = true;
        updateTestExportButton();
        let scroll = overlay.querySelector('.export-preview-scroll');
        if (!scroll && content) {
            scroll = document.createElement('div');
            scroll.className = 'export-preview-scroll';
            scroll.innerHTML = content.innerHTML;
            content.innerHTML = '';
            content.appendChild(scroll);
        }
        bindExportPreviewPanHandlers(scroll);
        bindExportPreviewZoomControls(overlay);
    }

    if (canReuseCachedExportPreview(img)) {
        const scroll = overlay?.querySelector('.export-preview-scroll');
        if (scroll) {
            scroll.innerHTML = '';
            const exportImg = document.createElement('img');
            exportImg.src = lastExportPreview.blobUrl;
            exportImg.className = 'export-preview-image';
            exportImg.dataset.blobUrl = lastExportPreview.blobUrl;
            exportImg.draggable = false;
            exportImg.onload = () => {
                overlay.classList.remove('loading');
                const { width, height } = getExportOutputDimensions(img);
                updateExportPreviewSizeFromImage(overlay, exportImg, lastExportPreview.sizeBytes, width, height);
                updateExportPreviewZoomUI(overlay, exportImg);
                updatePreviewZoomLabels();
            };
            scroll.appendChild(exportImg);
        }
        return;
    }

    const scroll = overlay?.querySelector('.export-preview-scroll');
    const existingImage = scroll ? scroll.querySelector('.export-preview-image') : null;
    const sameImage = lastExportPreview && lastExportPreview.imageId === img.id;
    if (overlay) {
        overlay.classList.remove('loading');
        overlay.dataset.requestId = String(requestId);
    }
    if (overlay) {
        const sizeEl = overlay.querySelector('.export-preview-size');
        if (sizeEl) {
            sizeEl.textContent = '';
            sizeEl.style.color = 'var(--text-secondary)';
        }
    }
    if (existingImage && preserveImage && sameImage) {
        overlay.classList.add('loading');
    } else if (scroll) {
        scroll.innerHTML = '<div style="color: var(--text-secondary);">Processing...</div>';
    }

    // Process image
    const processingSnapshot = createProcessingSnapshot(img);
    processImage(img, { cancelOnChange: true, snapshot: processingSnapshot, yieldMs: 1 }).then(blob => {
        if (!overlay || overlay.dataset.requestId !== String(requestId)) {
            return;
        }
        const blobUrl = URL.createObjectURL(blob);
        const { width, height } = getExportOutputDimensions(img);
        lastExportPreview = {
            imageId: img.id,
            cropChangeId: img._lastCropChangeId || 0,
            settingsChangeId: exportSettingsChangeId,
            blobUrl,
            sizeBytes: blob.size,
            width,
            height
        };
        if (existingImage && preserveImage && sameImage) {
            const previousUrl = existingImage.dataset.blobUrl;
            if (previousUrl) {
                URL.revokeObjectURL(previousUrl);
            }
            existingImage.onload = () => {
                if (!overlay || overlay.dataset.requestId !== String(requestId)) {
                    return;
                }
                overlay.classList.remove('loading');
                updateExportPreviewSizeFromImage(overlay, existingImage, blob.size, width, height);
                updateExportPreviewZoomUI(overlay, existingImage);
                updatePreviewZoomLabels();
            };
            existingImage.draggable = false;
            existingImage.src = blobUrl;
            existingImage.dataset.blobUrl = blobUrl;
        } else {
            const exportImg = document.createElement('img');
            exportImg.src = blobUrl;
            exportImg.className = 'export-preview-image';
            exportImg.dataset.blobUrl = blobUrl;
            exportImg.draggable = false;
            exportImg.onload = () => {
                if (!overlay || overlay.dataset.requestId !== String(requestId)) {
                    return;
                }
                if (scroll) {
                    scroll.innerHTML = '';
                    scroll.appendChild(exportImg);
                }
                overlay.classList.remove('loading');
                updateExportPreviewSizeFromImage(overlay, exportImg, blob.size, width, height);
                updateExportPreviewZoomUI(overlay, exportImg);
                updatePreviewZoomLabels();
            };
        }
    }).catch(err => {
        if (!overlay || overlay.dataset.requestId !== String(requestId)) {
            return;
        }
        if (err && (err.code === 'INTERRUPTED' || err.message === 'Interrupted')) {
            if (scroll) {
                scroll.innerHTML = '<div style="color: var(--warning);">Interrupted</div>';
            }
            overlay.classList.remove('loading');
            return;
        }
        if (scroll) {
            scroll.innerHTML = `<div style="color: var(--danger);">Error: ${err.message}</div>`;
        }
        overlay.classList.remove('loading');
    });
}

function refreshExportPreviewIfVisible(options = {}) {
    if (!isExportPreviewVisible || !selectedImageId) return;
    const overlay = document.querySelector('.export-preview-overlay');
    if (!overlay) return;
    const img = images.find(i => i.id === selectedImageId);
    if (!img) return;
    openExportPreview(img, options);
}

function scheduleExportPreviewRefresh(options = {}, delay = 180, changeId = null) {
    if (delay === null) {
        return;
    }
    if (changeId !== null) {
        exportPreviewLastScheduledChangeId = changeId;
    }
    if (exportPreviewDebounceTimer) {
        clearTimeout(exportPreviewDebounceTimer);
    }
    exportPreviewDebounceTimer = setTimeout(() => {
        exportPreviewDebounceTimer = null;
        if (changeId !== null) {
            exportPreviewLastRenderedChangeId = changeId;
        }
        refreshExportPreviewIfVisible(options);
    }, delay);
}

function bumpExportSettingsChange() {
    exportSettingsChangeId += 1;
    if (showGridSizeInfo) {
        updateGridInfoLabels();
    }
}

function canReuseCachedExportPreview(img) {
    if (!lastExportPreview.blobUrl) return false;
    if (lastExportPreview.imageId !== img.id) return false;
    const currentCropId = img._lastCropChangeId || 0;
    if (lastExportPreview.cropChangeId !== currentCropId) return false;
    if (lastExportPreview.settingsChangeId !== exportSettingsChangeId) return false;
    return true;
}

function computeScalePercent(imgEl) {
    if (!imgEl || !imgEl.naturalWidth || !imgEl.naturalHeight) return null;
    const rect = imgEl.getBoundingClientRect();
    if (!rect.width || !rect.height) return null;
    const scale = Math.min(rect.width / imgEl.naturalWidth, rect.height / imgEl.naturalHeight);
    return Math.round(scale * 100);
}

function updateTitleWithZoom(titleEl, imgEl) {
    if (!titleEl) return;
    const baseTitle = titleEl.dataset.baseTitle || titleEl.textContent;
    const percent = computeScalePercent(imgEl);
    const titleTextEl = titleEl.querySelector('.preview-title-text');
    const titleZoomEl = titleEl.querySelector('.preview-title-zoom');
    if (titleTextEl && titleZoomEl) {
        titleTextEl.textContent = baseTitle;
        if (percent && percent < 100) {
            titleZoomEl.textContent = `[${percent}%]`;
        } else {
            titleZoomEl.textContent = '';
        }
        return;
    }
    if (percent && percent < 100) {
        titleEl.textContent = `${baseTitle} [${percent}%]`;
    } else {
        titleEl.textContent = baseTitle;
    }
}

function updatePreviewZoomLabels() {
    const previewTitle = document.getElementById('previewTitle');
    const previewImage = document.querySelector('#previewWrapper .preview-image');
    updateTitleWithZoom(previewTitle, previewImage);

    const exportTitle = document.querySelector('.export-preview-title');
    const exportImage = document.querySelector('.export-preview-image');
    updateTitleWithZoom(exportTitle, exportImage);
    const overlay = document.querySelector('.export-preview-overlay');
    if (overlay && exportImage) {
        updateExportPreviewZoomUI(overlay, exportImage);
    }
}

function resizeWithCanvas(sourceCanvas, width, height) {
    const destCanvas = document.createElement('canvas');
    destCanvas.width = width;
    destCanvas.height = height;
    const destCtx = destCanvas.getContext('2d', { alpha: true });
    destCtx.imageSmoothingEnabled = true;
    destCtx.imageSmoothingQuality = 'high';
    destCtx.drawImage(sourceCanvas, 0, 0, width, height);
    return destCtx.getImageData(0, 0, width, height);
}

async function ensureOxipngReady() {
    if (!oxipngReady) {
        oxipngReady = initOxipng();
    }
    await oxipngReady;
}

function encodeWithCanvasFallback(imageData, format) {
    return new Promise((resolve, reject) => {
        const canvas = document.createElement('canvas');
        canvas.width = imageData.width;
        canvas.height = imageData.height;
        const ctx = canvas.getContext('2d', { alpha: true });
        ctx.putImageData(imageData, 0, 0);

        let quality;
        let mimeType;
        switch (format) {
            case 'jpeg':
                mimeType = 'image/jpeg';
                quality = parseInt(document.getElementById('jpegQuality').value) / 100;
                break;
            case 'webp':
                mimeType = 'image/webp';
                quality = parseInt(document.getElementById('webpQuality').value) / 100;
                break;
            case 'avif':
                mimeType = 'image/avif';
                quality = parseInt(document.getElementById('avifQuality').value) / 100;
                break;
            case 'png':
                mimeType = 'image/png';
                quality = 1;
                break;
            case 'gif':
                mimeType = 'image/png';
                quality = 1;
                break;
            default:
                mimeType = 'image/png';
                quality = 1;
        }

        canvas.toBlob((blob) => {
            if (blob) {
                resolve(blob);
            } else {
                reject(new Error('Failed to create blob'));
            }
        }, mimeType, quality);
    });
}

async function encodeWithJsquash(imageData, format) {
    switch (format) {
        case 'jpeg':
            return {
                mimeType: 'image/jpeg',
                data: await encodeJpeg(imageData, {
                    quality: parseInt(document.getElementById('jpegQuality').value)
                })
            };
        case 'webp':
            return {
                mimeType: 'image/webp',
                data: await encodeWebp(imageData, {
                    quality: parseInt(document.getElementById('webpQuality').value)
                })
            };
        case 'avif':
            return {
                mimeType: 'image/avif',
                data: await encodeAvif(imageData, {
                    quality: parseInt(document.getElementById('avifQuality').value)
                })
            };
        case 'png':
            await ensureOxipngReady();
            {
                const data = optimiseOxiPngRaw(
                    imageData.data,
                    imageData.width,
                    imageData.height,
                    OXIPNG_DEFAULTS.level,
                    OXIPNG_DEFAULTS.interlace,
                    OXIPNG_DEFAULTS.optimiseAlpha
                );
                return {
                    mimeType: 'image/png',
                    data: data instanceof Uint8Array ? data : new Uint8Array(data)
                };
            }
        case 'gif':
            await ensureOxipngReady();
            {
                const data = optimiseOxiPngRaw(
                    imageData.data,
                    imageData.width,
                    imageData.height,
                    OXIPNG_DEFAULTS.level,
                    OXIPNG_DEFAULTS.interlace,
                    OXIPNG_DEFAULTS.optimiseAlpha
                );
                return {
                    mimeType: 'image/png',
                    data: data instanceof Uint8Array ? data : new Uint8Array(data)
                };
            }
        default:
            await ensureOxipngReady();
            {
                const data = optimiseOxiPngRaw(
                    imageData.data,
                    imageData.width,
                    imageData.height,
                    OXIPNG_DEFAULTS.level,
                    OXIPNG_DEFAULTS.interlace,
                    OXIPNG_DEFAULTS.optimiseAlpha
                );
                return {
                    mimeType: 'image/png',
                    data: data instanceof Uint8Array ? data : new Uint8Array(data)
                };
            }
    }
}

// High-quality resize and encoding with jSquash or Photon
async function processImage(img, options = {}) {
    try {
        const { cancelOnChange = false, snapshot, yieldMs = 0, shouldCancel = null } = options;
        const processingSnapshot = snapshot || createProcessingSnapshot(img);
        if (cancelOnChange) {
            assertNotInterrupted(processingSnapshot, img);
        }
        assertNotCancelled(shouldCancel);
        const { usePhotonResize, jsquashMethod, photonSamplingFilter } = getResizeEngineSettings();
        const resizeEngine = usePhotonResize ? 'Photon' : 'jSquash';
        console.log(`=== Processing Image with ${resizeEngine} ===`);
        console.time('processImage total');
        const resizeMode = getResizeMode();
        const { width: exportWidth, height: exportHeight, shouldResize, crop } = getExportOutputDimensions(img, resizeMode);
        const format = document.getElementById('exportFormat').value;
        console.log(`Target: ${exportWidth}x${exportHeight}, Format: ${format}`);

        const fileToProcess = img.file;
        if (!fileToProcess) {
            throw new Error('No file available to process');
        }

        const cropRect = crop || img.crop;
        console.log(`Source crop: ${cropRect.width}x${cropRect.height} at (${cropRect.x}, ${cropRect.y})`);

        console.time('processImage decode');
        const imageBitmap = await createImageBitmap(fileToProcess);
        console.timeEnd('processImage decode');
        if (yieldMs > 0) {
            await yieldToUI(yieldMs);
        }
        if (cancelOnChange) {
            assertNotInterrupted(processingSnapshot, img);
        }
        assertNotCancelled(shouldCancel);
        console.time('processImage crop');
        const cropCanvas = document.createElement('canvas');
        cropCanvas.width = Math.round(cropRect.width);
        cropCanvas.height = Math.round(cropRect.height);
        const cropCtx = cropCanvas.getContext('2d', { alpha: true });
        cropCtx.drawImage(
            imageBitmap,
            cropRect.x, cropRect.y, cropRect.width, cropRect.height,
            0, 0, cropCanvas.width, cropCanvas.height
        );
        imageBitmap.close();

        const croppedImageData = cropCtx.getImageData(0, 0, cropCanvas.width, cropCanvas.height);
        console.timeEnd('processImage crop');
        console.log(`Cropped ImageData: ${croppedImageData.width}x${croppedImageData.height}`);
        if (yieldMs > 0) {
            await yieldToUI(yieldMs);
        }
        if (cancelOnChange) {
            assertNotInterrupted(processingSnapshot, img);
        }
        assertNotCancelled(shouldCancel);

        let resizedImageData = croppedImageData;
        const needsResize = shouldResize
            && exportWidth
            && exportHeight
            && (croppedImageData.width !== exportWidth || croppedImageData.height !== exportHeight);
        if (needsResize) {
            try {
                console.time('processImage resize');
                const forcedFallback = getForcedFallbackMode();
                if (forcedFallback === 'resize' || forcedFallback === 'both') {
                    throw new Error('Forced fallback (resize)');
                }
                if (usePhotonResize) {
                    resizedImageData = await resizeWithPhoton(
                        croppedImageData,
                        exportWidth,
                        exportHeight,
                        photonSamplingFilter
                    );
                    console.log('Photon resize complete');
                } else {
                    resizedImageData = await jsquashResize(croppedImageData, {
                        width: exportWidth,
                        height: exportHeight,
                        method: jsquashMethod || DEFAULT_JSQUASH_METHOD
                    });
                    console.log('jSquash resize complete');
                }
                console.timeEnd('processImage resize');
            } catch (resizeError) {
                console.timeEnd('processImage resize');
                console.warn(`${resizeEngine} resize failed, falling back to canvas resize`, resizeError);
                setFallbackWarning('resize');
                console.time('processImage resizeCanvas');
                resizedImageData = resizeWithCanvas(cropCanvas, exportWidth, exportHeight);
                console.timeEnd('processImage resizeCanvas');
            }
            if (yieldMs > 0) {
                await yieldToUI(yieldMs);
            }
        } else if (!shouldResize) {
            console.log(`Resize skipped (${getResizeMode()})`);
        } else if (shouldResize && exportWidth && exportHeight) {
            console.log('Resize skipped (already at target size)');
        }
        if (yieldMs > 0) {
            await yieldToUI(yieldMs);
        }
        if (cancelOnChange) {
            assertNotInterrupted(processingSnapshot, img);
        }
        assertNotCancelled(shouldCancel);

        try {
            const enableSharpening = Boolean(document.getElementById('enableSharpening')?.checked);
            let finalImageData = resizedImageData;
            if (enableSharpening) {
                console.time('processImage sharpen');
                finalImageData = await applySharpening(resizedImageData);
                console.timeEnd('processImage sharpen');
                if (yieldMs > 0) {
                    await yieldToUI(yieldMs);
                }
                if (cancelOnChange) {
                    assertNotInterrupted(processingSnapshot, img);
                }
                assertNotCancelled(shouldCancel);
            } else {
                console.log('Sharpening skipped (disabled)');
            }
            console.time('processImage encode');
            const forcedFallback = getForcedFallbackMode();
            if (forcedFallback === 'encode' || forcedFallback === 'both') {
                throw new Error('Forced fallback (encode)');
            }
            const { data, mimeType } = await encodeWithJsquash(finalImageData, format);
            console.timeEnd('processImage encode');
            const blob = new Blob([data], { type: mimeType });
            console.log(`${resizeEngine} blob created: ${blob.size} bytes`);
            console.log('=== Processing Complete ===');
            console.timeEnd('processImage total');
            return blob;
        } catch (encodeError) {
            console.warn(`${resizeEngine} encode failed, falling back to canvas encoding`, encodeError);
            setFallbackWarning('encode');
            console.time('processImage encodeFallback');
            const blob = await encodeWithCanvasFallback(resizedImageData, format);
            console.timeEnd('processImage encodeFallback');
            console.log(`Fallback blob created: ${blob.size} bytes`);
            console.log('=== Processing Complete ===');
            console.timeEnd('processImage total');
            return blob;
        }
    } catch (err) {
        console.timeEnd('processImage total');
        console.error('Process image error:', err);
        throw err;
    }
}

async function exportImages() {
    if (exportInProgress || images.length === 0) return;
    exportCancelRequested = false;
    setExportingState(true);

    const isSingleExport = images.length === 1;
    const zip = isSingleExport ? null : new JSZip();
    const usedFilenames = isSingleExport ? null : new Set();
    const format = document.getElementById('exportFormat').value;
    const progress = document.getElementById('exportProgress');
    const progressBar = document.getElementById('exportProgressBar');
    const progressText = document.getElementById('exportProgressText');
    let processedCount = 0;
    let singleExportBlob = null;
    let singleExportFilename = '';
    if (progress) {
        progress.classList.add('active');
    }
    if (progressBar) {
        progressBar.style.width = '0%';
    }
    if (progressText) {
        progressText.textContent = `Processing Image 1/${images.length}`;
    }

    try {
        for (let i = 0; i < images.length; i++) {
            if (exportCancelRequested) {
                assertNotCancelled(() => true);
            }
            const img = images[i];
            const blob = await processImage(img, {
                cancelOnChange: false,
                shouldCancel: () => exportCancelRequested,
                yieldMs: 1
            });

            const nameWithoutExt = img.name.replace(/\.[^/.]+$/, "");
            const baseFilename = `${nameWithoutExt}.${format}`;
            const filename = isSingleExport
                ? baseFilename
                : getUniqueFilename(baseFilename, usedFilenames);

            if (isSingleExport) {
                singleExportBlob = blob;
                singleExportFilename = filename;
            } else {
                zip.file(filename, blob);
            }
            processedCount += 1;
            if (progressBar) {
                progressBar.style.width = `${Math.round(((i + 1) / images.length) * 100)}%`;
            }
            if (progressText) {
                progressText.textContent = `Processing Image ${i + 1}/${images.length}`;
            }
        }

        if (exportCancelRequested) {
            assertNotCancelled(() => true);
        }

        if (progressText) {
            progressText.textContent = 'Preparing download...';
        }

        const content = isSingleExport ? singleExportBlob : await zip.generateAsync({ type: 'blob' });
        const url = URL.createObjectURL(content);
        const a = document.createElement('a');
        a.href = url;
        a.download = isSingleExport ? singleExportFilename : 'processed-images.zip';
        a.click();
        URL.revokeObjectURL(url);
        clearDirtyState();
        if (progressBar) {
            progressBar.style.width = '100%';
        }
    } catch (err) {
        if (err?.code === 'CANCELLED') {
            if (isSingleExport) {
                if (progressText) {
                    progressText.textContent = 'Export cancelled';
                }
                if (progressBar) {
                    progressBar.style.width = '0%';
                }
            } else if (processedCount > 0) {
                const shouldDownload = window.confirm('Download images produced so far?');
                if (shouldDownload) {
                    if (progressText) {
                        progressText.textContent = 'Preparing download...';
                    }
                    const content = await zip.generateAsync({ type: 'blob' });
                    const url = URL.createObjectURL(content);
                    const a = document.createElement('a');
                    a.href = url;
                    a.download = 'processed-images-partial.zip';
                    a.click();
                    URL.revokeObjectURL(url);
                    if (progressBar) {
                        progressBar.style.width = '100%';
                    }
                } else {
                    if (progressText) {
                        progressText.textContent = 'Export cancelled';
                    }
                    if (progressBar) {
                        progressBar.style.width = '0%';
                    }
                }
            } else {
                if (progressText) {
                    progressText.textContent = 'Export cancelled';
                }
                if (progressBar) {
                    progressBar.style.width = '0%';
                }
            }
        } else {
            console.error('Export failed:', err);
            if (progressText) {
                progressText.textContent = 'Export failed';
            }
        }
    } finally {
        exportCancelRequested = false;
        setExportingState(false);
        if (progress) {
            setTimeout(() => {
                progress.classList.remove('active');
                if (progressBar) {
                    progressBar.style.width = '0%';
                }
                if (progressText) {
                    progressText.textContent = '';
                }
            }, 400);
        }
    }
}

function getUniqueFilename(filename, usedFilenames) {
    if (!usedFilenames.has(filename)) {
        usedFilenames.add(filename);
        return filename;
    }
    const lastDotIndex = filename.lastIndexOf('.');
    const hasExt = lastDotIndex > 0 && lastDotIndex < filename.length - 1;
    const base = hasExt ? filename.slice(0, lastDotIndex) : filename;
    const ext = hasExt ? filename.slice(lastDotIndex) : '';
    let counter = 2;
    let candidate = `${base} (${counter})${ext}`;
    while (usedFilenames.has(candidate)) {
        counter += 1;
        candidate = `${base} (${counter})${ext}`;
    }
    usedFilenames.add(candidate);
    return candidate;
}

function adjustZoom(delta) {
    const step = 40;
    const direction = delta >= 0 ? 1 : -1;
    gridItemSize = Math.max(160, Math.min(800, gridItemSize + direction * step));
    updateGridSize();
    persistPreferencesNoDirty({ gridItemSize });
}

function resetZoom() {
    gridItemSize = 240;
    updateGridSize();
    persistPreferencesNoDirty({ gridItemSize });
}

function toggleTheme() {
    const enabled = document.getElementById('themeToggle')?.checked;
    document.documentElement.classList.toggle('light', Boolean(enabled));
    document.body.classList.toggle('light', Boolean(enabled));
    persistPreferencesNoDirty();
}

function updateGridSize() {
    const grid = document.getElementById('imagesGrid');
    if (!grid) return;
    grid.style.setProperty('--grid-image-size', `${gridItemSize}px`);
    grid.style.gridTemplateColumns = `repeat(auto-fill, minmax(${gridItemSize}px, 1fr))`;
    if (showFilenameTip) {
        positionFilenameTip();
    }
}

function alignAllCrops(alignment) {
    images.forEach(img => {
        switch(alignment) {
            case 'top':
                img.crop.y = 0;
                break;
            case 'middle':
                img.crop.y = (img.height - img.crop.height) / 2;
                break;
            case 'bottom':
                img.crop.y = img.height - img.crop.height;
                break;
        }
    });
    updateAllCropBoxes();
}

function setAspectRatio(width, height, buttonEl) {
    const mode = getResizeMode();
    if (mode !== RESIZE_MODES.FIXED_ASPECT && mode !== RESIZE_MODES.FIXED_ASPECT_CROP) return;
    currentAspectRatio = width / height;

    // Update all crops to new aspect ratio
    images.forEach(img => {
        img.crop = calculateInitialCrop(img.width, img.height);
    });

    if (mode === RESIZE_MODES.FIXED_ASPECT || mode === RESIZE_MODES.FIXED_ASPECT_CROP) {
        // Update export dimensions
        const exportWidth = parseInt(document.getElementById('exportWidth').value, 10);
        if (Number.isFinite(exportWidth) && exportWidth > 0) {
            const exportHeight = Math.round(exportWidth / currentAspectRatio);
            document.getElementById('exportHeight').value = exportHeight;
        }
    }

    // Update active button
    updateActiveAspectButton(buttonEl, width, height);
    const aspectW = document.getElementById('aspectW');
    const aspectH = document.getElementById('aspectH');
    if (aspectW && aspectH) {
        aspectW.value = width;
        aspectH.value = height;
    }
    updateAspectMismatchUI();
    persistPreferences({ aspectRatio: { width, height } });
    bumpExportSettingsChange();
    markProfileModified();

    updateAllCropBoxes();
    scheduleExportPreviewRefresh({ preserveImage: true }, getPreviewDelayMs(true));
}

function handleResizeModeChange() {
    updateResizeModeUI();
    updateResizeModeInfoText();
    updatePreviewNudgeControls();
    persistPreferences();
    markProfileModified();
    bumpExportSettingsChange();
    scheduleExportPreviewRefresh({ preserveImage: true }, getPreviewDelayMs(true));
}

function updateFormatSettings(shouldPersist = true) {
    const format = document.getElementById('exportFormat').value;

    document.getElementById('jpegSettings').style.display = format === 'jpeg' ? 'block' : 'none';
    document.getElementById('pngSettings').style.display = format === 'png' ? 'block' : 'none';
    document.getElementById('webpSettings').style.display = format === 'webp' ? 'block' : 'none';
    document.getElementById('avifSettings').style.display = format === 'avif' ? 'block' : 'none';
    document.getElementById('gifSettings').style.display = format === 'gif' ? 'block' : 'none';
    if (shouldPersist) {
        persistPreferences();
        markProfileModified();
    }
    bumpExportSettingsChange();
    scheduleExportPreviewRefresh({ preserveImage: true }, getPreviewDelayMs(true));
}

function toggleDrawer() {
    const drawer = document.getElementById('drawer');
    const icon = document.getElementById('drawerToggleIcon');
    drawer.classList.toggle('collapsed');
    icon.textContent = drawer.classList.contains('collapsed') ? '' : '';
}

function updateExportButton() {
    document.getElementById('exportBtn').disabled = images.length === 0 || exportInProgress;
}

function updateExportCancelButton() {
    const cancelBtn = document.getElementById('exportCancelBtn');
    if (!cancelBtn) return;
    cancelBtn.disabled = !exportInProgress || exportCancelRequested;
    cancelBtn.textContent = exportCancelRequested ? 'Cancelling...' : 'Cancel';
}

function setExportingState(isExporting) {
    exportInProgress = isExporting;
    document.body.classList.toggle('exporting', isExporting);
    updateExportButton();
    updateExportCancelButton();
}

function cancelExport() {
    if (!exportInProgress) return;
    exportCancelRequested = true;
    updateExportCancelButton();
    const progressText = document.getElementById('exportProgressText');
    if (progressText) {
        progressText.textContent = 'Cancelling...';
    }
}

function removeImage(id) {
    images = images.filter(img => img.id !== id);
    const grid = document.getElementById('imagesGrid');
    if (grid) {
        const card = grid.querySelector(`[data-image-id="${id}"]`);
        if (card) {
            card.remove();
        }
    }
    if (selectedImageId === id) {
        selectedImageId = null;
        closeExportPreview();
        closePreview();
        if (isSplitPreview) {
            setSplitPreview(false);
        }
    }
    updateExportButton();
    if (images.length === 0) {
        if (dropZone) {
            dropZone.classList.remove('hidden');
        }
        syncImagesVisibility();
        forceResetPreviewState();
    }
    setDirtyState(true);
}

function clearAllImages() {
    images = [];
    selectedImageId = null;
    closeExportPreview();
    closePreview();
    renderImages();
    updateExportButton();
    if (dropZone) {
        dropZone.classList.remove('hidden');
    }
    syncImagesVisibility();
    forceResetPreviewState();
    setDirtyState(true);
}

function handleResizeSettingsChange() {
    syncResizeEngineControls();
    persistPreferences();
    bumpExportSettingsChange();
    scheduleExportPreviewRefresh({ preserveImage: true }, getPreviewDelayMs(true));
}

