window.addEventListener('pageshow', () => {
    scheduleGridRefreshAfterResume();
});
window.addEventListener('resize', () => {
    if (showFilenameTip) {
        positionFilenameTip();
    }
});
document.getElementById('profileImportInput').addEventListener('change', async (event) => {
    const input = event.target;
    const file = input?.files?.[0];
    if (!file) return;
    try {
        const text = await file.text();
        const parsed = JSON.parse(text);
        if (!Array.isArray(parsed)) {
            window.alert('Invalid profiles JSON. Expected an array of profiles.');
            return;
        }
        const importedProfiles = parsed.filter(profile => profile && typeof profile === 'object');
        if (!importedProfiles.length) {
            window.alert('No valid profiles found in the JSON.');
            return;
        }
        const existingProfiles = getProfiles();
        const duplicates = collectDuplicateNames(importedProfiles, existingProfiles);
        if (duplicates.length) {
            const message = `Duplicate profile names detected:\n${duplicates.join(', ')}\n\nIf you continue, the current profiles will be overridden.`;
            const shouldContinue = window.confirm(message);
            if (!shouldContinue) {
                return;
            }
        }
        const mergedProfiles = mergeImportedProfiles(importedProfiles, existingProfiles);
        setProfiles(mergedProfiles);
        if (profilesVisible) {
            renderProfiles();
        }
    } catch (err) {
        console.error('Profile import failed', err);
        window.alert('Failed to import profiles. Please check the file and try again.');
    } finally {
        input.value = '';
    }
});
document.getElementById('sharpenRadiusNumber').addEventListener('input', () => {
    persistPreferences();
    bumpExportSettingsChange();
    markProfileModified();
    scheduleExportPreviewRefresh({ preserveImage: true }, getPreviewDelayMs(true));
});
document.getElementById('sharpenThresholdNumber').addEventListener('input', () => {
    persistPreferences();
    bumpExportSettingsChange();
    markProfileModified();
    scheduleExportPreviewRefresh({ preserveImage: true }, getPreviewDelayMs(true));
});
window.addEventListener('resize', updatePreviewZoomLabels);
bindRangeAndNumber('jpegQuality', 'jpegQualityNumber', (v) => parseInt(v, 10));
bindRangeAndNumber('webpQuality', 'webpQualityNumber', (v) => parseInt(v, 10));
bindRangeAndNumber('avifQuality', 'avifQualityNumber', (v) => parseInt(v, 10));
bindRangeAndNumber('sharpenAmount', 'sharpenAmountNumber', (v) => parseInt(v, 10));
bindRangeAndNumber('sharpenRadius', 'sharpenRadiusNumber', (v) => parseFloat(v));
bindRangeAndNumber('sharpenThreshold', 'sharpenThresholdNumber', (v) => parseInt(v, 10));

function getPreferences() {
    try {
        const raw = localStorage.getItem(STORAGE_KEY);
        return raw ? JSON.parse(raw) : {};
    } catch (err) {
        return {};
    }
}

function getProfiles() {
    const prefs = getPreferences();
    if (!Array.isArray(prefs.profiles)) {
        return [];
    }
    return prefs.profiles;
}

function setProfiles(profiles) {
    persistPreferences({ profiles });
    updateProfileImportExportButtons();
}

function mergeProfileDefaults(profile) {
    const merged = { ...profileDefault, ...profile };
    if (profileDefault.aspectRatio || profile.aspectRatio) {
        merged.aspectRatio = {
            ...(profileDefault.aspectRatio || {}),
            ...(profile.aspectRatio || {})
        };
    }
    return merged;
}

function buildDefaultProfileName() {
    const width = document.getElementById('exportWidth').value;
    const height = document.getElementById('exportHeight').value;
    const scalePercent = getScalePercent();
    const format = document.getElementById('exportFormat').value.toUpperCase();
    const mode = getResizeMode();
    const aspectW = parseFloat(document.getElementById('aspectW').value);
    const aspectH = parseFloat(document.getElementById('aspectH').value);
    const hasAspectInputs = Number.isFinite(aspectW) && Number.isFinite(aspectH) && aspectH > 0;
    let aspect = '';
    if ((mode === RESIZE_MODES.FIXED_ASPECT || mode === RESIZE_MODES.STATIC_SIZE) && width && height) {
        aspect = `${width}:${height}`;
    } else if (mode === RESIZE_MODES.FIXED_ASPECT_CROP && hasAspectInputs) {
        aspect = `${aspectW}:${aspectH}`;
    }
    const parts = [];
    if (mode === RESIZE_MODES.SCALED) {
        parts.push(`${scalePercent}%`);
    } else if (width && height && mode !== RESIZE_MODES.FIXED_ASPECT_CROP) {
        parts.push(`${width}x${height}`);
    }
    if (aspect) {
        parts.push(`${aspect}`);
    }
    parts.push(format);
    return parts.join(' ');
}

function formatAspectRatio(profile) {
    if (profile.aspectRatio && profile.aspectRatio.width && profile.aspectRatio.height) {
        const w = profile.aspectRatio.width;
        const h = profile.aspectRatio.height;
        const { arW, arH } = aspectRatioExact(w, h, { includePortrait: false });
        for (const preset of ASPECT_PRESETS) {
            if (preset.w === arW && preset.h === arH) {
                return preset.label || `${preset.w}:${preset.h}`;
            }
        }
        return simplifyRatioString(w, h);
    }
    if (profile.exportWidth && profile.exportHeight) {
        return simplifyRatioString(profile.exportWidth, profile.exportHeight);
    }
    return '';
}

function renderAspectPresetButtons() {
    const select = document.getElementById('aspectPresetSelect');
    if (!select) return;
    select.innerHTML = '';
    const placeholder = document.createElement('option');
    placeholder.value = '';
    placeholder.textContent = 'Select preset';
    select.appendChild(placeholder);
    ASPECT_PRESETS.slice(0, 30).forEach(preset => {
        const option = document.createElement('option');
        option.value = `${preset.w}:${preset.h}`;
        option.dataset.aspectW = preset.w;
        option.dataset.aspectH = preset.h;
        option.textContent = preset.label || `${preset.w}:${preset.h}`;
        select.appendChild(option);
    });
    select.addEventListener('change', () => {
        const selected = select.selectedOptions[0];
        if (!selected || !selected.dataset.aspectW || !selected.dataset.aspectH) return;
        setAspectRatio(parseInt(selected.dataset.aspectW), parseInt(selected.dataset.aspectH));
    });
}

function updateProfileNameDisplay() {
    const input = document.getElementById('profileNameDisplay');
    if (!input) return;
    if (!currentProfileName) {
        input.textContent = '(no profile selected)';
        input.classList.add('muted');
    } else {
        input.textContent = profileModified ? `${currentProfileName} (modified)` : currentProfileName;
        input.classList.remove('muted');
    }
    const clearAction = document.getElementById('profileClearAction');
    const revertAction = document.getElementById('profileRevertAction');
    if (clearAction) {
        clearAction.style.display = currentProfileName ? 'inline' : 'none';
    }
    if (revertAction) {
        revertAction.style.display = profileModified && lastLoadedProfile ? 'inline' : 'none';
    }
}

function markProfileModified() {
    if (suppressProfileModified) return;
    profileModified = true;
    updateProfileNameDisplay();
}

function captureCurrentProfileSettings() {
    return {
        exportWidth: parseInt(document.getElementById('exportWidth').value),
        exportHeight: parseInt(document.getElementById('exportHeight').value),
        scalePercent: getScalePercent(),
        resizeMode: getResizeMode(),
        aspectRatio: { width: currentAspectRatio * 1000, height: 1000 },
        exportFormat: document.getElementById('exportFormat').value,
        jpegQuality: parseInt(document.getElementById('jpegQuality').value),
        webpQuality: parseInt(document.getElementById('webpQuality').value),
        avifQuality: parseInt(document.getElementById('avifQuality').value),
        pngTransparency: document.getElementById('pngTransparency').checked,
        enableSharpening: document.getElementById('enableSharpening').checked,
        adaptiveSharpening: document.getElementById('adaptiveSharpening').checked,
        sharpenAmount: parseInt(document.getElementById('sharpenAmount').value),
        sharpenRadius: parseFloat(document.getElementById('sharpenRadius').value),
        sharpenThreshold: parseInt(document.getElementById('sharpenThreshold').value)
    };
}

function applyProfileSettings(profile, options = {}) {
    if (!profile) return;
    const { applyDefaults = false, setProfileName = true, setLastLoaded = true } = options;
    const resolvedProfile = applyDefaults ? mergeProfileDefaults(profile) : profile;
    let resizeMode = resolvedProfile.resizeMode;
    if (!resizeMode && typeof resolvedProfile.lockAspect === 'boolean') {
        resizeMode = resolvedProfile.lockAspect ? RESIZE_MODES.FIXED_ASPECT : RESIZE_MODES.STATIC_SIZE;
    }
    resizeMode = resizeMode || DEFAULT_RESIZE_MODE;
    suppressProfileModified = true;
    if (Number.isFinite(resolvedProfile.exportWidth)) {
        document.getElementById('exportWidth').value = resolvedProfile.exportWidth;
    }
    if (Number.isFinite(resolvedProfile.exportHeight)) {
        document.getElementById('exportHeight').value = resolvedProfile.exportHeight;
    }
    if (Number.isFinite(resolvedProfile.scalePercent)) {
        const scaleInput = document.getElementById('scalePercent');
        if (scaleInput) {
            scaleInput.value = clampScalePercent(resolvedProfile.scalePercent);
        }
    }
    const resizeModeSelect = document.getElementById('resizeMode');
    if (resizeModeSelect) {
        resizeModeSelect.value = resizeMode;
        if (!resizeModeSelect.value) {
            resizeModeSelect.value = DEFAULT_RESIZE_MODE;
            resizeMode = DEFAULT_RESIZE_MODE;
        }
    }
    if (resolvedProfile.exportFormat) {
        document.getElementById('exportFormat').value = resolvedProfile.exportFormat;
    }
    if (Number.isFinite(resolvedProfile.jpegQuality)) {
        document.getElementById('jpegQuality').value = resolvedProfile.jpegQuality;
        document.getElementById('jpegQualityNumber').value = resolvedProfile.jpegQuality;
    }
    if (Number.isFinite(resolvedProfile.webpQuality)) {
        document.getElementById('webpQuality').value = resolvedProfile.webpQuality;
        document.getElementById('webpQualityNumber').value = resolvedProfile.webpQuality;
    }
    if (Number.isFinite(resolvedProfile.avifQuality)) {
        document.getElementById('avifQuality').value = resolvedProfile.avifQuality;
        document.getElementById('avifQualityNumber').value = resolvedProfile.avifQuality;
    }
    if (typeof resolvedProfile.pngTransparency === 'boolean') {
        document.getElementById('pngTransparency').checked = resolvedProfile.pngTransparency;
    }
    if (typeof resolvedProfile.enableSharpening === 'boolean') {
        document.getElementById('enableSharpening').checked = resolvedProfile.enableSharpening;
    }
    if (typeof resolvedProfile.adaptiveSharpening === 'boolean') {
        document.getElementById('adaptiveSharpening').checked = resolvedProfile.adaptiveSharpening;
    }
    if (Number.isFinite(resolvedProfile.sharpenAmount)) {
        document.getElementById('sharpenAmount').value = resolvedProfile.sharpenAmount;
        document.getElementById('sharpenAmountNumber').value = resolvedProfile.sharpenAmount;
    }
    if (Number.isFinite(resolvedProfile.sharpenRadius)) {
        document.getElementById('sharpenRadius').value = resolvedProfile.sharpenRadius;
        document.getElementById('sharpenRadiusNumber').value = resolvedProfile.sharpenRadius;
    }
    if (Number.isFinite(resolvedProfile.sharpenThreshold)) {
        document.getElementById('sharpenThreshold').value = resolvedProfile.sharpenThreshold;
        document.getElementById('sharpenThresholdNumber').value = resolvedProfile.sharpenThreshold;
    }

    if (resolvedProfile.aspectRatio && Number.isFinite(resolvedProfile.aspectRatio.width) && Number.isFinite(resolvedProfile.aspectRatio.height)) {
        currentAspectRatio = resolvedProfile.aspectRatio.width / resolvedProfile.aspectRatio.height;
        updateActiveAspectButton(null, resolvedProfile.aspectRatio.width, resolvedProfile.aspectRatio.height);
        const aspectW = document.getElementById('aspectW');
        const aspectH = document.getElementById('aspectH');
        if (aspectW && aspectH) {
            aspectW.value = resolvedProfile.aspectRatio.width;
            aspectH.value = resolvedProfile.aspectRatio.height;
        }
    } else {
        const width = parseInt(document.getElementById('exportWidth').value);
        const height = parseInt(document.getElementById('exportHeight').value);
        if (width && height) {
            currentAspectRatio = width / height;
            updateAspectInputsFromCurrent();
        }
    }

    if (isAspectLockedMode(resizeMode)) {
        images.forEach(img => {
            img.crop = calculateInitialCrop(img.width, img.height);
        });
    }

    updateFormatSettings(false);
    handleScalePercentInput(false);
    lastResizeMode = resizeMode;
    updateResizeModeUI();
    persistPreferences();
    if (shouldShowCropOverlay(resizeMode)) {
        updateAllCropBoxes();
    }
    bumpExportSettingsChange();
    scheduleExportPreviewRefresh({ preserveImage: true }, getPreviewDelayMs(true));
    currentProfileName = setProfileName ? (resolvedProfile.name || '') : '';
    profileModified = false;
    lastLoadedProfile = setLastLoaded ? { ...resolvedProfile } : null;
    updateProfileNameDisplay();
    suppressProfileModified = false;
}

function clearProfileName() {
    currentProfileName = '';
    profileModified = false;
    updateProfileNameDisplay();
}

function revertProfileSettings() {
    if (!lastLoadedProfile) return;
    applyProfileSettings(lastLoadedProfile);
}

function resetProfileToDefault() {
    applyProfileSettings(profileDefault, {
        applyDefaults: true,
        setProfileName: false,
        setLastLoaded: false
    });
}

function renderProfiles() {
    const list = document.getElementById('profilesList');
    if (!list) return;
    const profiles = getProfiles();
    list.innerHTML = '';
    updateProfileImportExportButtons();
    const createFoldableSection = (titleText, isOpen) => {
        const section = document.createElement('section');
        section.className = `profiles-section ${isOpen ? 'expanded' : 'collapsed'}`;

        const title = document.createElement('button');
        title.type = 'button';
        title.className = 'profiles-section-title';
        title.setAttribute('aria-expanded', isOpen ? 'true' : 'false');

        const label = document.createElement('span');
        label.className = 'profiles-section-label';
        label.textContent = titleText;
        title.appendChild(label);

        const chevron = document.createElement('span');
        chevron.className = 'profiles-section-chevron';
        chevron.textContent = '';
        title.appendChild(chevron);

        const body = document.createElement('div');
        body.className = 'profiles-section-body';

        title.onclick = () => {
            const expanded = section.classList.toggle('expanded');
            section.classList.toggle('collapsed', !expanded);
            title.setAttribute('aria-expanded', expanded ? 'true' : 'false');
        };

        section.appendChild(title);
        section.appendChild(body);
        return { section, body };
    };

    const addProfileCard = (profile, idx, allowDelete) => {
        const card = document.createElement('div');
        card.className = 'profile-card';
        const info = document.createElement('div');
        info.className = 'profile-info';
        const name = document.createElement('div');
        name.className = 'profile-name';
        name.textContent = profile.name || `Profile ${idx + 1}`;
        const meta = document.createElement('div');
        meta.className = 'profile-meta';
        const aspect = formatAspectRatio(profile);
        const parts = [
            `${profile.exportWidth || ''}x${profile.exportHeight || ''}`.trim(),
            String(profile.exportFormat || '').toUpperCase(),
            aspect
        ].filter(Boolean);
        if (profile.enableSharpening) {
            parts.push('Sharpening');
        }
        if (profile.resizeMode === RESIZE_MODES.SCALED && Number.isFinite(profile.scalePercent)) {
            parts.push(`${clampScalePercent(profile.scalePercent)}%`);
        }
        meta.textContent = parts.join('  ');
        info.appendChild(name);
        info.appendChild(meta);
        const actions = document.createElement('div');
        actions.className = 'profile-actions';
        const loadBtn = document.createElement('button');
        loadBtn.className = 'btn';
        loadBtn.textContent = 'Load';
        loadBtn.onclick = () => {
            applyProfileSettings(profile);
            if (profilesVisible) {
                toggleProfiles();
            }
        };
        actions.appendChild(loadBtn);
        if (allowDelete) {
            const deleteBtn = document.createElement('button');
            deleteBtn.className = 'btn';
            deleteBtn.textContent = 'Delete';
            deleteBtn.onclick = () => {
                const profileName = profile.name || `Profile ${idx + 1}`;
                const confirmed = window.confirm(`Delete profile "${profileName}"?`);
                if (!confirmed) return;
                const next = getProfiles().filter((_, pIdx) => pIdx !== idx);
                setProfiles(next);
                renderProfiles();
            };
            actions.appendChild(deleteBtn);
        }
        card.appendChild(info);
        card.appendChild(actions);
        return card;
    };

    const userList = document.createElement('div');
    userList.className = 'profiles-list';
    if (!profiles.length) {
        const empty = document.createElement('div');
        empty.style.color = 'var(--text-secondary)';
        empty.style.fontSize = '12px';
        empty.textContent = 'No profiles saved yet.';
        userList.appendChild(empty);
    } else {
        profiles.forEach((profile, idx) => userList.appendChild(addProfileCard(profile, idx, true)));
    }
    const userSection = createFoldableSection('User Profiles', true);
    userSection.body.appendChild(userList);
    list.appendChild(userSection.section);

    const groupedDefaults = Array.isArray(DEFAULT_PROFILES)
        ? [{ title: 'Default Profiles', profiles: DEFAULT_PROFILES }]
        : Object.entries(DEFAULT_PROFILES || {})
            .map(([title, groupedProfiles]) => ({
                title,
                profiles: Array.isArray(groupedProfiles) ? groupedProfiles : []
            }))
            .filter(group => group.profiles.length > 0);

    groupedDefaults.forEach((group, groupIdx) => {
        const groupList = document.createElement('div');
        groupList.className = 'profiles-list';
        group.profiles.forEach((profile, idx) => {
            const card = addProfileCard(profile, idx, false);
            const loadBtn = card.querySelector('button.btn');
            if (loadBtn) {
                loadBtn.onclick = () => {
                    applyProfileSettings(profile, { applyDefaults: true });
                    if (profilesVisible) {
                        toggleProfiles();
                    }
                };
            }
            groupList.appendChild(card);
        });
        const section = createFoldableSection(group.title || `Profiles ${groupIdx + 1}`, false);
        section.body.appendChild(groupList);
        list.appendChild(section.section);
    });
}

function updateProfileImportExportButtons() {
    const exportBtn = document.getElementById('exportProfilesBtn');
    if (!exportBtn) return;
    const profiles = getProfiles();
    exportBtn.disabled = !profiles.length;
}

function exportUserProfiles() {
    const profiles = getProfiles();
    if (!profiles.length) return;
    const payload = JSON.stringify(profiles, null, 2);
    const blob = new Blob([payload], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'batch-image-profiles.json';
    document.body.appendChild(link);
    link.click();
    link.remove();
    URL.revokeObjectURL(url);
}

function importUserProfiles() {
    const input = document.getElementById('profileImportInput');
    if (!input) return;
    input.value = '';
    input.click();
}

function normalizeProfileName(profile) {
    if (!profile || typeof profile !== 'object') return '';
    if (typeof profile.name !== 'string') return '';
    return profile.name.trim();
}

function collectDuplicateNames(importedProfiles, existingProfiles) {
    const duplicates = new Set();
    const seenImported = new Set();
    const existingNames = new Set(existingProfiles.map(normalizeProfileName).filter(Boolean));

    importedProfiles.forEach((profile) => {
        const name = normalizeProfileName(profile);
        if (!name) return;
        if (seenImported.has(name) || existingNames.has(name)) {
            duplicates.add(name);
        }
        seenImported.add(name);
    });

    return Array.from(duplicates);
}

function mergeImportedProfiles(importedProfiles, existingProfiles) {
    const merged = [...existingProfiles];
    const nameIndex = new Map();
    merged.forEach((profile, idx) => {
        const name = normalizeProfileName(profile);
        if (name) {
            nameIndex.set(name, idx);
        }
    });

    importedProfiles.forEach((profile) => {
        if (!profile || typeof profile !== 'object') return;
        const name = normalizeProfileName(profile);
        if (name && nameIndex.has(name)) {
            merged[nameIndex.get(name)] = profile;
        } else {
            merged.push(profile);
            if (name) {
                nameIndex.set(name, merged.length - 1);
            }
        }
    });

    return merged;
}

function toggleProfiles() {
    profilesVisible = !profilesVisible;
    const panel = document.getElementById('profilesPanel');
    const btn = document.getElementById('openProfilesBtn');
    const loadBtn = document.getElementById('loadProfileBtn');
    if (panel) {
        panel.classList.toggle('active', profilesVisible);
    }
    syncImagesVisibility();
    if (btn) {
        btn.textContent = profilesVisible ? 'Back to Images' : 'Open Profiles';
        btn.classList.toggle('active', profilesVisible);
    }
    if (loadBtn) {
        loadBtn.textContent = profilesVisible ? 'Back to images' : 'Load Profile';
        loadBtn.classList.toggle('active', profilesVisible);
    }
    if (profilesVisible) {
        renderProfiles();
    }
    updateFilenameTipVisibility();
}

function saveProfile() {
    const defaultName = buildDefaultProfileName();
    const promptName = currentProfileName || defaultName;
    const name = window.prompt('Profile name', promptName);
    if (name === null) return;
    const trimmed = name.trim() || currentProfileName || defaultName;
    const profile = {
        name: trimmed,
        ...captureCurrentProfileSettings()
    };
    const profiles = getProfiles();
    const existingIndex = profiles.findIndex((p) => p.name === trimmed);
    if (existingIndex !== -1) {
        const overwrite = window.confirm(`Profile "${trimmed}" exists. Overwrite it?`);
        if (!overwrite) return;
        profiles.splice(existingIndex, 1, profile);
    } else {
        profiles.push(profile);
    }
    setProfiles(profiles);
    currentProfileName = trimmed;
    profileModified = false;
    lastLoadedProfile = { ...profile };
    updateProfileNameDisplay();
    if (profilesVisible) {
        renderProfiles();
    }
}
function bindRangeAndNumber(rangeId, numberId, parseFn = (v) => Number(v)) {
    const rangeEl = document.getElementById(rangeId);
    const numberEl = document.getElementById(numberId);
    if (!rangeEl || !numberEl) return;

    const min = Number(rangeEl.min);
    const max = Number(rangeEl.max);
    const clamp = (value) => Math.min(max, Math.max(min, value));

    const syncFromRange = () => {
        numberEl.value = rangeEl.value;
    };
    const syncFromNumber = () => {
        const parsed = parseFn(numberEl.value);
        if (!Number.isFinite(parsed)) return;
        const clamped = clamp(parsed);
        rangeEl.value = clamped;
        numberEl.value = clamped;
    };

    rangeEl.addEventListener('input', () => {
        syncFromRange();
    });
    numberEl.addEventListener('input', () => {
        syncFromNumber();
    });

    syncFromRange();
}

function setDirtyState(nextDirty) {
    isDirty = nextDirty;
}

function clearDirtyState() {
    setDirtyState(false);
}

function persistPreferencesNoDirty(patch = {}) {
    const prevSuppress = suppressDirty;
    suppressDirty = true;
    persistPreferences(patch);
    suppressDirty = prevSuppress;
}

function persistPreferences(patch = {}) {
    const prefs = getPreferences();
    const resizeSettings = getResizeEngineSettings();
    const exportWidthInput = document.getElementById('exportWidth');
    const exportHeightInput = document.getElementById('exportHeight');
    const exportWidthValue = parseInt(exportWidthInput?.value);
    const exportHeightValue = parseInt(exportHeightInput?.value);
    const stashedWidth = parseInt(exportWidthInput?.dataset.stashedValue);
    const stashedHeight = parseInt(exportHeightInput?.dataset.stashedValue);
    const resolvedWidth = Number.isFinite(exportWidthValue)
        ? exportWidthValue
        : (Number.isFinite(stashedWidth) ? stashedWidth : null);
    const resolvedHeight = Number.isFinite(exportHeightValue)
        ? exportHeightValue
        : (Number.isFinite(stashedHeight) ? stashedHeight : null);
    const next = {
        ...prefs,
        exportWidth: resolvedWidth,
        exportHeight: resolvedHeight,
        scalePercent: getScalePercent(),
        resizeMode: getResizeMode(),
        exportFormat: document.getElementById('exportFormat').value,
        jpegQuality: parseInt(document.getElementById('jpegQuality').value),
        webpQuality: parseInt(document.getElementById('webpQuality').value),
        avifQuality: parseInt(document.getElementById('avifQuality').value),
        pngTransparency: document.getElementById('pngTransparency').checked,
        previewDelay: document.getElementById('previewDelay').value,
        lightMode: document.body.classList.contains('light'),
        lastExportPreviewMode,
        showAboutOnLoad: document.getElementById('aboutAutoShow').checked,
        enableSharpening: document.getElementById('enableSharpening').checked,
        adaptiveSharpening: document.getElementById('adaptiveSharpening').checked,
        sharpenAmount: parseInt(document.getElementById('sharpenAmount').value),
        sharpenRadius: parseFloat(document.getElementById('sharpenRadius').value),
        sharpenThreshold: parseInt(document.getElementById('sharpenThreshold').value),
        resizeEngine: resizeSettings.engine,
        usePhotonResize: resizeSettings.usePhotonResize,
        jsquashResizeMethod: resizeSettings.jsquashMethod,
        photonSamplingFilter: resizeSettings.photonSamplingFilter,
        showGridSizeInfo,
        showFilenameTip,
        gridItemSize,
        ...patch
    };
    localStorage.setItem(STORAGE_KEY, JSON.stringify(next));
    if (!suppressDirty) {
        setDirtyState(true);
    }
}

function applyPreferences() {
    const prefs = getPreferences();
    const mobileWarning = document.getElementById('mobileWarning');
    if (mobileWarning) {
        if (prefs.mobileWarningDismissed) {
            mobileWarning.remove();
        } else {
            mobileWarning.classList.remove('hidden');
        }
    }
    document.documentElement.classList.add('no-theme-transition');
    suppressProfileModified = true;
    if (Number.isFinite(prefs.exportWidth)) {
        document.getElementById('exportWidth').value = prefs.exportWidth;
    }
    if (Number.isFinite(prefs.exportHeight)) {
        document.getElementById('exportHeight').value = prefs.exportHeight;
    }
    if (Number.isFinite(prefs.scalePercent)) {
        const scaleInput = document.getElementById('scalePercent');
        if (scaleInput) {
            scaleInput.value = clampScalePercent(prefs.scalePercent);
        }
    }
    let resizeMode = prefs.resizeMode;
    if (!resizeMode && typeof prefs.lockAspect === 'boolean') {
        resizeMode = prefs.lockAspect ? RESIZE_MODES.FIXED_ASPECT : RESIZE_MODES.STATIC_SIZE;
    }
    resizeMode = resizeMode || DEFAULT_RESIZE_MODE;
    const resizeModeSelect = document.getElementById('resizeMode');
    if (resizeModeSelect) {
        resizeModeSelect.value = resizeMode;
        if (!resizeModeSelect.value) {
            resizeModeSelect.value = DEFAULT_RESIZE_MODE;
        }
    }
    if (prefs.exportFormat) {
        document.getElementById('exportFormat').value = prefs.exportFormat;
    }
    if (Number.isFinite(prefs.jpegQuality)) {
        document.getElementById('jpegQuality').value = prefs.jpegQuality;
        document.getElementById('jpegQualityNumber').value = prefs.jpegQuality;
    }
    if (Number.isFinite(prefs.webpQuality)) {
        document.getElementById('webpQuality').value = prefs.webpQuality;
        document.getElementById('webpQualityNumber').value = prefs.webpQuality;
    }
    if (Number.isFinite(prefs.avifQuality)) {
        document.getElementById('avifQuality').value = prefs.avifQuality;
        document.getElementById('avifQualityNumber').value = prefs.avifQuality;
    }
    if (typeof prefs.pngTransparency === 'boolean') {
        document.getElementById('pngTransparency').checked = prefs.pngTransparency;
    }
    if (prefs.previewDelay) {
        document.getElementById('previewDelay').value = prefs.previewDelay;
    } else {
        document.getElementById('previewDelay').value = '0.5';
    }
    showGridSizeInfo = Boolean(prefs.showGridSizeInfo);
    showFilenameTip = prefs.showFilenameTip !== false;
    if (Number.isFinite(prefs.gridItemSize)) {
        gridItemSize = Math.max(160, Math.min(800, Math.round(prefs.gridItemSize)));
    }
    if (prefs.lastExportPreviewMode) {
        lastExportPreviewMode = prefs.lastExportPreviewMode;
        if (lastExportPreviewMode === 'split') {
            lastExportPreviewMode = 'split-horizontal';
        }
    }
    if (typeof prefs.enableSharpening === 'boolean') {
        document.getElementById('enableSharpening').checked = prefs.enableSharpening;
    }
    if (typeof prefs.adaptiveSharpening === 'boolean') {
        document.getElementById('adaptiveSharpening').checked = prefs.adaptiveSharpening;
    }
    if (Number.isFinite(prefs.sharpenAmount)) {
        document.getElementById('sharpenAmount').value = prefs.sharpenAmount;
        document.getElementById('sharpenAmountNumber').value = prefs.sharpenAmount;
    }
    if (Number.isFinite(prefs.sharpenRadius)) {
        document.getElementById('sharpenRadius').value = prefs.sharpenRadius;
        document.getElementById('sharpenRadiusNumber').value = prefs.sharpenRadius;
    }
    if (Number.isFinite(prefs.sharpenThreshold)) {
        document.getElementById('sharpenThreshold').value = prefs.sharpenThreshold;
        document.getElementById('sharpenThresholdNumber').value = prefs.sharpenThreshold;
    }
    let resizeEngine = prefs.resizeEngine;
    if (!resizeEngine) {
        resizeEngine = typeof prefs.usePhotonResize === 'boolean' && prefs.usePhotonResize ? 'photon' : 'jsquash';
    }
    const resizeEngineSelect = document.getElementById('resizeEngine');
    if (resizeEngineSelect) {
        resizeEngineSelect.value = resizeEngine;
        if (!resizeEngineSelect.value) {
            resizeEngineSelect.value = 'jsquash';
        }
    }
    const jsquashSelect = document.getElementById('jsquashResizeMethod');
    if (jsquashSelect) {
        const method = prefs.jsquashResizeMethod || DEFAULT_JSQUASH_METHOD;
        jsquashSelect.value = method;
        if (!jsquashSelect.value) {
            jsquashSelect.value = DEFAULT_JSQUASH_METHOD;
        }
    }
    const photonSelect = document.getElementById('photonResizeFilter');
    if (photonSelect) {
        const filter = Number.isFinite(prefs.photonSamplingFilter)
            ? String(Math.min(5, Math.max(1, prefs.photonSamplingFilter)))
            : String(DEFAULT_PHOTON_SAMPLING_FILTER);
        photonSelect.value = filter;
        if (!photonSelect.value) {
            photonSelect.value = String(DEFAULT_PHOTON_SAMPLING_FILTER);
        }
    }
    if (prefs.aspectRatio && Number.isFinite(prefs.aspectRatio.width) && Number.isFinite(prefs.aspectRatio.height)) {
        currentAspectRatio = prefs.aspectRatio.width / prefs.aspectRatio.height;
        updateActiveAspectButton(null, prefs.aspectRatio.width, prefs.aspectRatio.height);
        const aspectW = document.getElementById('aspectW');
        const aspectH = document.getElementById('aspectH');
        if (aspectW && aspectH) {
            aspectW.value = prefs.aspectRatio.width;
            aspectH.value = prefs.aspectRatio.height;
        }
    } else {
        const width = parseInt(document.getElementById('exportWidth').value);
        const height = parseInt(document.getElementById('exportHeight').value);
        if (width && height) {
            currentAspectRatio = width / height;
            updateAspectInputsFromCurrent();
        }
    }
    if (getResizeMode() === RESIZE_MODES.STATIC_SIZE) {
        const width = parseInt(document.getElementById('exportWidth').value);
        const height = parseInt(document.getElementById('exportHeight').value);
        if (Number.isFinite(width) && Number.isFinite(height) && height > 0) {
            currentAspectRatio = width / height;
            updateAspectInputsFromCurrent();
        }
    }

    updateFormatSettings(false);
    syncResizeEngineControls();
    updateResizeModeUI();
    syncAboutCheckbox();
    if (typeof prefs.lightMode === 'boolean') {
        document.documentElement.classList.toggle('light', prefs.lightMode);
        document.body.classList.toggle('light', prefs.lightMode);
        const themeToggle = document.getElementById('themeToggle');
        if (themeToggle) {
            themeToggle.checked = prefs.lightMode;
        }
    }
    requestAnimationFrame(() => {
        document.documentElement.classList.remove('no-theme-transition');
    });
    suppressProfileModified = false;
    updateProfileNameDisplay();
    updateGridInfoLabels();
    updateProfileImportExportButtons();
    updateResizeModeInfoText();
    updateFilenameTipVisibility();
    updateGridSize();
    updatePreviewNudgeControls();
}

function resetPreferences() {
    const existingProfiles = getProfiles();
    localStorage.removeItem(STORAGE_KEY);
    suppressProfileModified = true;

    const defaultWidth = Number.isFinite(profileDefault.exportWidth) ? profileDefault.exportWidth : 1920;
    const defaultHeight = Number.isFinite(profileDefault.exportHeight) ? profileDefault.exportHeight : 1080;
    const defaultAspect = profileDefault.aspectRatio
        && Number.isFinite(profileDefault.aspectRatio.width)
        && Number.isFinite(profileDefault.aspectRatio.height)
        ? profileDefault.aspectRatio
        : { width: defaultWidth, height: defaultHeight };

    document.getElementById('exportWidth').value = defaultWidth;
    document.getElementById('exportHeight').value = defaultHeight;
    const resizeModeSelect = document.getElementById('resizeMode');
    if (resizeModeSelect) {
        resizeModeSelect.value = profileDefault.resizeMode || DEFAULT_RESIZE_MODE;
        if (!resizeModeSelect.value) {
            resizeModeSelect.value = DEFAULT_RESIZE_MODE;
        }
    }
    document.getElementById('exportFormat').value = profileDefault.exportFormat || 'jpeg';
    document.getElementById('jpegQuality').value = Number.isFinite(profileDefault.jpegQuality) ? profileDefault.jpegQuality : 90;
    document.getElementById('webpQuality').value = Number.isFinite(profileDefault.webpQuality) ? profileDefault.webpQuality : 90;
    document.getElementById('avifQuality').value = Number.isFinite(profileDefault.avifQuality) ? profileDefault.avifQuality : 60;
    document.getElementById('jpegQualityNumber').value = Number.isFinite(profileDefault.jpegQuality) ? profileDefault.jpegQuality : 90;
    document.getElementById('webpQualityNumber').value = Number.isFinite(profileDefault.webpQuality) ? profileDefault.webpQuality : 90;
    document.getElementById('avifQualityNumber').value = Number.isFinite(profileDefault.avifQuality) ? profileDefault.avifQuality : 60;
    document.getElementById('pngTransparency').checked = typeof profileDefault.pngTransparency === 'boolean' ? profileDefault.pngTransparency : true;
    document.getElementById('previewDelay').value = '0.5';
    document.getElementById('aboutAutoShow').checked = true;
    document.getElementById('enableSharpening').checked = typeof profileDefault.enableSharpening === 'boolean' ? profileDefault.enableSharpening : false;
    document.getElementById('adaptiveSharpening').checked = typeof profileDefault.adaptiveSharpening === 'boolean' ? profileDefault.adaptiveSharpening : false;
    document.getElementById('sharpenAmount').value = Number.isFinite(profileDefault.sharpenAmount) ? profileDefault.sharpenAmount : 120;
    document.getElementById('sharpenRadius').value = Number.isFinite(profileDefault.sharpenRadius) ? profileDefault.sharpenRadius : 1.0;
    document.getElementById('sharpenThreshold').value = Number.isFinite(profileDefault.sharpenThreshold) ? profileDefault.sharpenThreshold : 10;
    document.getElementById('sharpenAmountNumber').value = Number.isFinite(profileDefault.sharpenAmount) ? profileDefault.sharpenAmount : 120;
    document.getElementById('sharpenRadiusNumber').value = Number.isFinite(profileDefault.sharpenRadius) ? profileDefault.sharpenRadius : 1.0;
    document.getElementById('sharpenThresholdNumber').value = Number.isFinite(profileDefault.sharpenThreshold) ? profileDefault.sharpenThreshold : 10;
    const resizeEngineSelect = document.getElementById('resizeEngine');
    if (resizeEngineSelect) {
        resizeEngineSelect.value = 'jsquash';
    }
    const jsquashSelect = document.getElementById('jsquashResizeMethod');
    if (jsquashSelect) {
        jsquashSelect.value = DEFAULT_JSQUASH_METHOD;
    }
    const photonSelect = document.getElementById('photonResizeFilter');
    if (photonSelect) {
        photonSelect.value = String(DEFAULT_PHOTON_SAMPLING_FILTER);
    }
    const scaleInput = document.getElementById('scalePercent');
    if (scaleInput) {
        const defaultScale = Number.isFinite(profileDefault.scalePercent)
            ? profileDefault.scalePercent
            : DEFAULT_SCALE_PERCENT;
        scaleInput.value = clampScalePercent(defaultScale);
    }
    const aspectW = document.getElementById('aspectW');
    const aspectH = document.getElementById('aspectH');
    if (aspectW && aspectH) {
        aspectW.value = defaultAspect.width;
        aspectH.value = defaultAspect.height;
    }
    const themeToggle = document.getElementById('themeToggle');
    if (themeToggle) {
        themeToggle.checked = false;
    }
    showGridSizeInfo = false;
    showFilenameTip = true;
    gridItemSize = 240;
    updateGridInfoLabels();
    document.documentElement.classList.remove('light');
    document.body.classList.remove('light');

    currentAspectRatio = defaultAspect.width / defaultAspect.height;
    updateActiveAspectButton(null, defaultAspect.width, defaultAspect.height);
    updateFormatSettings(false);
    syncResizeEngineControls();
    updateResizeModeUI();
    persistPreferencesNoDirty({
        aspectRatio: { width: defaultAspect.width, height: defaultAspect.height },
        showGridSizeInfo: false,
        showFilenameTip: true,
        gridItemSize: 240,
        profiles: existingProfiles
    });
    currentProfileName = '';
    profileModified = false;
    lastLoadedProfile = null;
    lastExportPreviewMode = 'none';
    suppressProfileModified = false;
    updateProfileNameDisplay();
}

function updateActiveAspectButton(buttonEl, width, height) {
    const select = document.getElementById('aspectPresetSelect');
    if (!select) return;
    const match = Array.from(select.options).find(option => {
        const w = parseInt(option.dataset.aspectW);
        const h = parseInt(option.dataset.aspectH);
        return w === width && h === height;
    });
    select.value = match ? match.value : '';
}

function syncAboutCheckbox() {
    const prefs = getPreferences();
    const checkbox = document.getElementById('aboutAutoShow');
    if (typeof prefs.showAboutOnLoad === 'boolean') {
        checkbox.checked = prefs.showAboutOnLoad;
    } else {
        checkbox.checked = true;
    }
}

function shouldShowAboutOnLoad() {
    const prefs = getPreferences();
    if (typeof prefs.showAboutOnLoad === 'boolean') {
        return prefs.showAboutOnLoad;
    }
    return true;
}

